.container {
  display: flex;
  justify-content: center;
  margin-top: 180px;
  width: 100vw;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 480px;
}

.link {
  color: #4c4cff;
  text-decoration: none;
  transition: 0.3s;
}

.link:hover {
  color: white;
  transition: 0.3s;
}
